<template>
    <div class="board">
        <h1
            class="text-end"
            style="
                border-style: solid;
                border-color: var(--bs-blue);
                border-radius: 0;
                border-top-left-radius: 3;
                border-top-right-radius: 3;
                border-bottom-right-radius: 3;
                border-bottom-left-radius: 3;
            "
        >
            {{ showResult }}
        </h1>
        <div class="row">
            <div class="col">
                <button
                    @click="
                        result = '0';
                        hasdot = false;
                        op = '';
                        opp = 0;
                    "
                    class="btn btn-primary mybtn"
                    type="button"
                    style="
                        color: var(--bs-body-color);
                        background: var(--bs-gray-500);
                        border-style: none;
                        border-color: var(--bs-body-bg);
                        border-top-left-radius: 4;
                        border-top-right-radius: 4;
                        border-bottom-right-radius: 4;
                        border-bottom-left-radius: 4;
                        text-shadow: 0px 0px;
                        transform: perspective(0px) translate(0px) rotate(0deg)
                            scale(1);
                        filter: blur(0px);
                    "
                >
                    AC
                </button>
            </div>
            <div class="col">
                <button
                    @click="minus()"
                    class="btn btn-primary mybtn"
                    type="button"
                    style="
                        color: var(--bs-body-color);
                        background: var(--bs-gray-500);
                        border-style: none;
                    "
                >
                    +/-
                </button>
            </div>
            <div class="col">
                <button
                    class="btn btn-primary mybtn"
                    type="button"
                    style="
                        color: var(--bs-body-color);
                        background: var(--bs-gray-500);
                        border-style: none;
                    "
                >
                    %
                </button>
            </div>
            <div class="col">
                <button
                    @click="oper('/')"
                    class="btn btn-primary mybtn"
                    type="button"
                    style="
                        border-style: none;
                        background: var(--bs-orange);
                        color: var(--bs-body-bg);
                    "
                >
                    ÷
                </button>
            </div>
        </div>
        <div class="row">
            <div class="col">
                <button
                    @click="digit(7)"
                    class="btn btn-primary mybtn"
                    type="button"
                    style="
                        color: var(--bs-body-bg);
                        background: var(--bs-gray-900);
                        border-style: none;
                    "
                >
                    7
                </button>
            </div>
            <div class="col">
                <button
                    @click="digit(8)"
                    class="btn btn-primary mybtn"
                    type="button"
                    style="
                        color: var(--bs-body-bg);
                        background: var(--bs-gray-900);
                        border-style: none;
                    "
                >
                    8
                </button>
            </div>
            <div class="col">
                <button
                    @click="digit(9)"
                    class="btn btn-primary mybtn"
                    type="button"
                    style="
                        color: var(--bs-body-bg);
                        background: var(--bs-gray-900);
                        border-style: none;
                    "
                >
                    9
                </button>
            </div>
            <div class="col">
                <button
                    @click="oper('*')"
                    class="btn btn-primary mybtn"
                    type="button"
                    style="background: var(--bs-orange); border-style: none"
                >
                    ×
                </button>
            </div>
        </div>
        <div class="row">
            <div class="col">
                <button
                    @click="digit(4)"
                    class="btn btn-primary mybtn"
                    type="button"
                    style="
                        color: var(--bs-body-bg);
                        background: var(--bs-gray-900);
                        border-style: none;
                    "
                >
                    4
                </button>
            </div>
            <div class="col">
                <button
                    @click="digit(5)"
                    class="btn btn-primary mybtn"
                    type="button"
                    style="
                        color: var(--bs-body-bg);
                        background: var(--bs-gray-900);
                        border-style: none;
                    "
                >
                    5
                </button>
            </div>
            <div class="col">
                <button
                    @click="digit(6)"
                    class="btn btn-primary mybtn"
                    type="button"
                    style="
                        color: var(--bs-body-bg);
                        background: var(--bs-gray-900);
                        border-style: none;
                    "
                >
                    6
                </button>
            </div>
            <div class="col">
                <button
                    @click="oper('-')"
                    class="btn btn-primary mybtn"
                    type="button"
                    style="background: var(--bs-orange); border-style: none"
                >
                    －
                </button>
            </div>
        </div>
        <div class="row">
            <div class="col">
                <button
                    @click="digit(1)"
                    class="btn btn-primary mybtn"
                    type="button"
                    style="
                        color: var(--bs-body-bg);
                        background: var(--bs-gray-900);
                        border-style: none;
                    "
                >
                    1
                </button>
            </div>
            <div class="col">
                <button
                    @click="digit(2)"
                    class="btn btn-primary mybtn"
                    type="button"
                    style="
                        color: var(--bs-body-bg);
                        background: var(--bs-gray-900);
                        border-style: none;
                    "
                >
                    2
                </button>
            </div>
            <div class="col">
                <button
                    @click="digit(3)"
                    class="btn btn-primary mybtn"
                    type="button"
                    style="
                        color: var(--bs-body-bg);
                        background: var(--bs-gray-900);
                        border-style: none;
                    "
                >
                    3
                </button>
            </div>
            <div class="col">
                <button
                    @click="oper('+')"
                    class="btn btn-primary mybtn"
                    type="button"
                    style="
                        color: var(--bs-body-bg);
                        background: var(--bs-orange);
                        border-style: solid;
                    "
                >
                    +
                </button>
            </div>
        </div>
        <div class="row">
            <div class="col">
                <button
                    @click="digit(0)"
                    class="btn btn-primary mybtn"
                    type="button"
                    style="
                        color: var(--bs-body-bg);
                        background: var(--bs-gray-900);
                        border-style: none;
                    "
                >
                    0
                </button>
            </div>
            <div class="col">
                <button
                    @click="digit(-1)"
                    class="btn btn-primary mybtn"
                    type="button"
                    style="
                        color: var(--bs-body-bg);
                        background: var(--bs-gray-900);
                        border-style: none;
                    "
                >
                    00
                </button>
            </div>
            <div class="col">
                <button
                    @click="dot()"
                    class="btn btn-primary mybtn"
                    type="button"
                    style="
                        color: var(--bs-body-bg);
                        background: var(--bs-gray-900);
                        border-style: none;
                    "
                >
                    .
                </button>
            </div>
            <div class="col">
                <button
                    @click="oper('=')"
                    class="btn btn-primary mybtn"
                    type="button"
                    style="
                        color: var(--bs-body-bg);
                        background: var(--bs-orange);
                        border-style: solid;
                    "
                >
                    =
                </button>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "App",
    data() {
        return {
            result: "0",
            first: "0",
            op: "",
            hasdot: false,
            opp: 0,
        };
    },
    computed: {
        showResult() {
            let res = Number(this.result).toFixed(6);
            console.log("RES", res);
            return res.toString();
        },
    },
    methods: {
        digit(n) {
            if (this.opp == 1) {
                this.result = "";
                this.opp = 0;
            }
            if (this.result == "0") {
                this.result = "";
            }
            if (n >= 0) {
                this.result = this.result + n.toString();
            } else {
                if (Number(this.result) !== 0) {
                    this.result = this.result + "00";
                } else {
                    this.result = "0";
                }
            }
        },
        dot() {
            if (this.opp == 1) {
                this.result = "";
                this.opp = 0;
            }
            if (this.hasdot) return;
            this.hasdot = true;
            this.result += ".";
        },
        minus() {
            if (this.result.substring(0, 1) == "-") {
                this.result = this.result.substr(1);
            } else {
                this.result = "-" + this.result;
            }
        },
        compu() {
            if (this.op == "+") {
                console.log("res", this.result);
                console.log("first", this.first);
                this.result = (
                    Number(this.result) + Number(this.first)
                ).toString();
                console.log("res", this.result);
            }
            if (this.op == "-") {
                this.result = (
                    Number(this.first) - Number(this.result)
                ).toString();
            }
            if (this.op == "*") {
                this.result = (
                    Number(this.result) * Number(this.first)
                ).toString();
            }
            if (this.op == "/") {
                this.result = (
                    Number(this.first) / Number(this.result)
                ).toString();
            }
            this.op = "";
        },
        oper(p) {
            this.hasdot = false;
            if (this.op !== "") this.compu();
            if (p !== "=") this.op = p;
            this.opp = 1;
            this.first = this.result;
        },
    },
};
</script>

<style scoped>
.mybtn {
    width: 50px;
    display: inherit;
    height: 50px;
    margin: 6px auto;
    text-align: center;
    border-radius: 50px;
}

.board {
    max-width: 600px;
    background: var(--bs-gray-700);
    padding: 20px;
}

h1 {
    color: white;
}
</style>
